.comments {
    margin-top: 5%;
    margin-left: 20px;
}

.darker {
    border: 1px solid #ecb21f;
    background-color: black;
    float: right;
    border-radius: 5px;
    padding-left: 40px;
    padding-right: 30px;
    padding-top: 10px;
}

.comment {
    border: 1px solid rgba(16, 46, 46, 1);
    background-color: rgba(16, 46, 46, 0.973);
    float: left;
    border-radius: 5px;
    padding-left: 40px;
    padding-right: 30px;
    padding-top: 10px;

}

.comment h4,
.comment span,
.darker h4,
.darker span {
    display: inline;
}

.comment p,
.comment span,
.darker p,
.darker span {
    color: rgb(184, 183, 183);
}

h3,
h6 {
    color: white;
    font-weight: bold;
}

.comment-container label {
    color: rgb(212, 208, 208);
}

.comment-container #align-form {
    margin-top: 20px;
}

#darker img {
    margin-right: 15px;
    position: static;
}